import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  public innerHtml;
  public height;

  constructor(private sanitizer: DomSanitizer) { }

  ngOnInit(): void {

    const html = `<div>
               <script src="https://cdn.htmlgames.com/embed.js?game=BubbleShooter&amp;&amp;bgcolor=white"></script>
               </div>
               <script>
               var nodes = document.getElementById('ZMgameDiv').childNodes;
               for(var i=0;i<nodes.length;i++){
                if(nodes[i].childNodes.length>0){
                  for(var id=0;id<nodes[i].childNodes.length;id++){
                  if(nodes[i].childNodes[id].tagName=='IMG')nodes[i].childNodes[id].click();
                    }
                  }
               if(nodes[i].innerText=='Play game'){
                nodes[i].style.display='none';
                }
              }
               </script>`;

    this.height = window.innerHeight+' px';

    setTimeout(() => {
      this.innerHtml = this.sanitizer.bypassSecurityTrustHtml(html);
    }, 1000);

  }

}
